:root {
  font-family: Arial, Helvetica, sans-serif;
}

.athletes {
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  gap: 40px 30px;
  display: grid;
}

@media (width <= 500px) {
  .athletes {
    grid-template-columns: repeat(2, 200px);
  }
}

.person {
  flex-direction: column;
  align-items: center;
  width: 200px;
  height: 231px;
  display: flex;
}

.person__photo {
  position: relative;
}

.person__image {
  border-radius: 200px;
  width: 200px;
  height: 200px;
}

.person__rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  font-size: 16px;
  display: flex;
  position: absolute;
  top: 84px;
  right: 10px;
}

.person__details {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.person__name {
  color: #2f4f4f;
  margin-top: 8px;
  font-size: 20px;
}

.person__extra {
  margin-top: 9px;
  font-size: 18px;
  display: none;
}

.person__sport:after {
  content: ", ";
  white-space: pre;
}

.person__pay:before {
  content: "$";
}

.person__pay:after {
  content: "M";
}

.person:hover {
  animation-name: move;
  animation-duration: 2s;
}

.person:hover .person__image {
  animation-name: radius;
  animation-duration: 2s;
  animation-delay: 1s;
}

.person:hover .person__extra {
  justify-content: center;
  align-items: center;
  display: flex;
}

@keyframes move {
  0% {
    background-color: #eee;
    border-radius: 10px;
    flex-direction: column;
    align-items: center;
    width: 200px;
    height: 231px;
    padding: 10px 10px 46px;
    display: flex;
  }

  100% {
    background-color: #eee;
    padding: 10px 10px 46px;
    transform: scale(1.2);
  }
}

@keyframes radius {
  0% {
    border-radius: 50%;
  }

  100% {
    border-radius: 60px;
  }
}
